.product {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    &__left {
        width: 100%;
        margin-bottom: 1rem;
        @include rmin($md) {
            width: 22rem; }
        @include rmin($lg) {
            width: 31.876rem; } }

    &__right {
        width: 100%;
        @include rmin($md) {
            width: calc(100% - 24rem); }
        @include rmin($lg) {
            width: calc(100% - 33.876rem); } }

    &__content {
        width: 100%;
        margin-top: 2rem; }


    &__rtitle {
        font-size: 1.125rem;
        font-family: 'MuseoBold';
        margin-bottom: 1rem; }
    &__prices {
        padding: 1rem 1.5rem;
        background: #ffffff;
        display: flex;
        margin-top: 2rem;
        justify-content: space-between; }
    &__price {
        color: $text;
        &--old {
            & span {
                text-decoration: line-through; } }
        & span {
            font-family: 'MuseoBold';
            font-size: 1.25rem; } }
    &__remain {
        font-size: 0.875rem;
        color: #98a9b4;
        margin-top: 1.5rem;
        text-align: center; }
    &__fcart {
        margin-top: 2rem; }
    &__count {
        margin-top: 2rem; }
    &__carousel {
        margin-top: 3rem;
        & .owl-dots {
            display: none !important; } }
    &__cnav {
        margin-bottom: 0.75rem; } }


.pch {
    &__item {
        color: #7e8b91;
        &:first-child {
            margin-bottom: 1rem; }
        & span {
            color: $text;
            font-family: 'MuseoMedium'; } } }

