/* Constants */
/* media */
html, body {
  font-family: 'MuseoLight';
  font-size: 15px;
  line-height: 1.4;
  color: #39464b;
  background: #f9f9f9;
}

@media only screen and (min-width: 576px) {
  html, body {
    font-size: 16px;
  }
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

a {
  color: #39464b;
  text-decoration: none;
  transition: all ease 0.3s;
}

a:hover {
  color: #409bd5;
}

p {
  color: #39464b;
  line-height: 1.375rem;
  margin-bottom: 1rem;
}

.overflow {
  max-width: 100%;
  overflow-x: scroll;
}

.icon {
  margin-right: 0.5rem;
}

.icon__svg {
  width: 1rem;
  height: 1.25rem;
  fill: #409bd5;
  vertical-align: sub;
}

.icon__svg--auth {
  fill: #8daec2;
}

.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 10px;
}

@media only screen and (min-width: 576px) {
  .container {
    padding: 0 20px;
  }
}

.title {
  font-size: 1.563rem;
  line-height: 1.3;
  margin-bottom: 1rem;
  font-family: 'MuseoBold';
}

.subtitle {
  font-size: 1.25rem;
  margin-bottom: 1.25rem;
}

.date {
  font-size: 0.875rem;
  color: #929090;
}

p {
  margin-bottom: 1rem;
}

p + img {
  margin-bottom: 1rem;
}

/* btn-big */
.btn-big {
  background: #409bd5;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 0.875rem;
  font-family: 'MuseoMedium';
  padding: 0.825rem 3.25rem 0.825rem 1.25rem;
  display: inline-block;
  position: relative;
}

@media only screen and (min-width: 768px) {
  .btn-big {
    padding: 1rem 3.75rem 1rem 1.75rem;
  }
}

.btn-big:hover {
  background: #256086;
  color: #ffffff;
}

.btn-big:hover:after {
  transform: translateX(0.5rem);
}

.btn-big:after {
  position: absolute;
  right: 1.75rem;
  top: calc(50% - 0.4125rem);
  width: 0.75rem;
  height: 1.25rem;
  content: ' ';
  background: url(../img/arr-right.svg) no-repeat;
  background-size: contain;
  opacity: 0.8;
  transition: transform ease 0.3s;
}

.btn-big--white {
  background: #ffffff;
  color: #409bd5;
}

.btn-big--white:hover {
  background: #256086;
  color: #ffffff;
}

.btn-big--white:after {
  background: url(../img/arr-right-blue.svg) no-repeat;
  background-size: contain;
}

/* subnav */
.subnav {
  display: none;
}

@media only screen and (min-width: 992px) {
  .subnav {
    display: block;
  }
}

.subnav--footer .subnav__item {
  margin-bottom: 0.25rem;
}

.subnav--footer .subnav__link {
  color: #e5edf0;
  text-decoration: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
  transition: all ease 0.2s;
}

.subnav--footer .subnav__link:hover {
  border-bottom: 1px solid transparent;
}

/* action-nav */
.action-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.action-nav__item {
  flex-grow: 1;
  flex: 1;
}

.action-nav__link {
  width: 100%;
  padding: .775rem;
  white-space: nowrap;
}

.action-nav__link:after {
  display: none;
}

@media only screen and (min-width: 768px) {
  .action-nav__link:after {
    display: block;
  }
}

/* Breadcrumbs */
.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  padding: 1.5rem 0;
  list-style: none;
  border-radius: .25rem;
}

.breadcrumb__item {
  font-size: 0.875rem;
}

.breadcrumb__item + .breadcrumb__item:before {
  display: inline-block;
  padding-right: .5rem;
  padding-left: .5rem;
  color: #6c757d;
  content: "/";
}

.breadcrumb__item a {
  text-decoration: underline;
  font-size: 0.875rem;
}

/* badges */
.badges {
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
}

.badges__list {
  display: flex;
  flex-wrap: wrap;
}

.badges__item {
  margin-right: 1rem;
  background-size: contain;
  background-repeat: no-repeat;
  font-size: 0.875rem;
  color: #ffffff;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.5rem;
  height: 3.5rem;
  font-family: 'MuseoMedium';
}

.badges__item--sale {
  background-image: url(../img/sticker.svg);
}

/* fcart */
.fcart {
  height: 100%;
  display: flex;
  background: #e6e6e6;
  align-items: center;
}

.fcart--product {
  background: transparent;
}

.fcart__btn {
  height: 100%;
  width: 100%;
  padding: 0.75rem 0.875rem 0.5rem 0.875rem;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  position: relative;
  background: #e6e6e6;
  transition: all ease 0.3s;
}

@media only screen and (min-width: 768px) {
  .fcart__btn {
    padding: 0.75rem 1.25rem;
  }
}

@media only screen and (min-width: 768px) {
  .fcart__btn {
    width: auto;
  }
}

.fcart__btn:hover {
  background: #409bd5;
  color: #ffffff;
  fill: #ffffff;
}

.fcart__btn--product {
  margin-right: 1rem;
  background: #409bd5;
  color: #ffffff;
  fill: #ffffff;
}

.fcart__btn--product:hover {
  background: #256086;
}

.fcart__hr {
  width: 1px;
  height: 1.5rem;
  background: #cccccc;
}

.fcart__icon {
  width: 1.125rem;
  height: 1.125rem;
}

.fcart__name {
  font-size: 0.875rem;
  font-family: 'MuseoMedium';
  margin-bottom: 0.125rem;
  margin-left: 0.5rem;
  white-space: nowrap;
}

/* btn */
.btn {
  padding: 0.625rem 0.875rem 0.625rem 0.875rem;
  background: #409bd5;
  color: #ffffff;
  fill: #ffffff;
  white-space: nowrap;
  display: block;
  font-size: 1rem;
  font-family: 'MuseoLight';
  transition: all ease 0.25s;
}

.btn--center {
  text-align: center;
}

.btn--bold {
  font-family: 'MuseoBold';
}

.btn--inline {
  display: inline-block;
}

.btn:hover {
  background: #256086;
  color: #ffffff;
}

.btn-icon {
  height: 100%;
  padding: 0.5rem 0.75rem 0.375rem 0.75rem;
  display: inline-block;
  position: relative;
  background: #409bd5;
  color: #ffffff;
  fill: #ffffff;
  white-space: nowrap;
  transition: all ease 0.3s;
}

.btn-icon:hover {
  background: #256086;
  color: #ffffff;
  fill: #ffffff;
}

.btn-icon i {
  display: inline-block;
}

.btn-icon__icon {
  width: 1.125rem;
  height: 1.125rem;
}

.btn-icon__name {
  font-size: 0.875rem;
  font-family: 'MuseoMedium';
  margin-bottom: 0.125rem;
  margin-left: 0.5rem;
  white-space: nowrap;
  vertical-align: text-bottom;
  display: inline-block;
}

.btn-trash__icon {
  width: 1.5rem;
  height: 1.5rem;
  fill: #c02323;
}

/* Form */
.form__group {
  margin-bottom: 1.5rem;
  width: 100%;
  max-width: 30rem;
}

.form__group--center {
  text-align: center;
}

.form__label {
  font-size: 0.875rem;
  width: 100%;
  margin-bottom: 0.125rem;
}

.form__input[type="text"] {
  font-size: 1rem;
  padding: 0.5rem 0.5rem;
  border: 1px solid #cecece;
  width: 100%;
}

.form__btn {
  background: #409bd5;
  color: #ffffff;
  padding: 0.625rem 2rem;
  font-size: 0.875rem;
}

.form__btn:hover {
  background: #256086;
}

.form__repass {
  margin-right: 2rem;
  color: #409bd5;
}

/* list */
.list li {
  margin-bottom: 0.125rem;
}

.list li a {
  color: #39464b;
  text-decoration: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
  transition: all ease 0.2s;
}

.list li a:hover {
  color: #409bd5;
  border-bottom: 1px solid transparent;
}

.social-btn {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: flex-end;
  margin-top: 0.75rem;
}

.social-btn p {
  color: #e5edf0;
  margin-bottom: 0;
  display: none;
}

@media only screen and (min-width: 576px) {
  .social-btn p {
    display: block;
  }
}

.social-btn__link {
  display: inline-flex;
  margin-left: 0.5rem;
  padding: 0.5rem;
  fill: #e5edf0;
  background: #4c5b65;
  transition: opacity ease 0.2s;
}

.social-btn__link:hover {
  opacity: 0.5;
}

.social-btn__svg {
  width: 14px;
  height: 14px;
}

/* Table */
.stable thead {
  border-bottom: 1px solid #cccccc;
}

.stable thead td {
  padding: 0.75rem 0;
  white-space: nowrap;
}

.stable tr {
  border-bottom: 1px solid #cccccc;
}

.stable tr:last-child {
  border: 0;
}

.stable tr td {
  text-align: center;
  padding: 0.5rem 0.5rem;
  font-size: 0.875rem;
  vertical-align: middle;
}

.stable tr td:last-child {
  text-align: right;
}

.stable tr td:first-child {
  text-align: left;
}

.stable tr td img {
  max-width: 8rem;
}

.stable__img {
  width: 8.75rem;
}

.stable__title {
  text-decoration: underline;
  font-family: 'MuseoBold';
}

.stable__remain {
  color: #555555;
  white-space: nowrap;
}

.stable__price {
  white-space: nowrap;
  font-family: 'MuseoBold';
}

.stable__cprice-old {
  font-family: 'MuseoBold';
  color: #777777;
  text-decoration: line-through;
  white-space: nowrap;
}

.stable__cprice-sale {
  font-family: 'MuseoBold';
  white-space: nowrap;
}

.stable__cprice-price {
  font-family: 'MuseoBold';
  white-space: nowrap;
}

.stable__sample {
  color: #409bd5;
}

.stable__sample:hover {
  color: #39464b;
}

/* count */
.count {
  display: flex;
  justify-content: center;
}

@media only screen and (min-width: 768px) {
  .count {
    justify-content: flex-start;
  }
}

.count--cart {
  justify-content: center;
}

.count__btn {
  background: #ededed;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  line-height: 1;
  display: inline-block;
}

@media only screen and (min-width: 768px) {
  .count__btn {
    flex-grow: 0;
  }
}

.count__count {
  background: #ffffff;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  line-height: 1;
  text-align: center;
  max-width: 4rem;
}

.count--cart .count__btn {
  background: transparent;
  padding: 0 0.375rem;
  font-size: 1rem;
  font-family: 'MuseoBold';
}

.count--cart .count__count {
  background: #ffffff;
  padding: 0.5rem 0.875rem;
  font-size: 0.875rem;
  width: 3rem;
  border: 1px solid #cccccc;
}

/* remodal */
.remodal {
  padding: 1.5rem 1.75rem 0.75rem;
  text-align: left;
}

.remodal--min {
  max-width: 500px;
}

.remodal-close {
  left: auto;
  right: 1rem;
  top: 1rem;
  height: 40px;
}

.remodal-close:before {
  font-size: 40px;
  line-height: 40px;
  color: #000000;
}

.remodal__title {
  font-size: 1.25rem;
  font-family: 'MuseoBold';
  margin-bottom: 1.5rem;
  text-align: left;
}

.remodal__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.remodal__item {
  width: 100%;
  margin-bottom: 0.25rem;
}

@media only screen and (min-width: 576px) {
  .remodal__item {
    width: calc(100% / 2 - 2rem);
  }
}

@media only screen and (min-width: 768px) {
  .remodal__item {
    width: calc(100% / 3 - 2rem);
  }
}

.remodal__item a {
  font-family: 'MuseoMedium';
}

.remodal__footer {
  margin-top: 1.5rem;
  padding-top: 0.75rem;
  border-top: 1px solid #cccccc;
  color: #999999;
  text-align: right;
  font-size: 0.875rem;
}

.remodal__footer--center {
  text-align: center;
}

.remodal__svg {
  width: 2rem;
  height: 1.25rem;
  fill: #409bd5;
  vertical-align: bottom;
}

/* mobile-menu (mmenu) */
.mmenu {
  width: 87%;
  max-width: 25rem;
  overflow-y: scroll;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 15;
  background: #f6f6f6;
  transform: translateX(-26rem);
  transition: transform ease 0.2s;
}

.mmenu__is-opened {
  box-shadow: 0 0 150px 10px rgba(0, 0, 0, 0.35);
  transform: translateX(0);
}

@media only screen and (min-width: 768px) {
  .mmenu__is-opened {
    box-shadow: none;
    transform: translateX(-26rem);
  }
}

.mmenu__top {
  display: flex;
  align-content: center;
  align-items: center;
  padding: 1.5rem 1rem 1rem 1rem;
  position: relative;
}

.mmenu__logo {
  max-width: 5rem;
  margin-top: 0.25rem;
}

.mmenu__phone {
  margin-left: 1.5rem !important;
}

.mmenu__auth {
  padding: 0.75rem 0;
  text-align: left !important;
  background: #ffffff;
  border-top: 1px solid #dddddd;
  border-bottom: 1px solid #dddddd;
}

.mmenu__nav {
  padding: 1.25rem;
  margin: 1.5rem 1.75rem 0;
  background: #ffffff;
  border: 1px solid #dddddd;
}

.mmenu__nav .list {
  margin-left: 1.75rem;
}

.mmenu__subnav {
  display: block;
}

.mmenu__city {
  padding: 0rem 3.125rem;
  margin-top: 1.5rem;
  display: flex;
}

.mmenu__city-select {
  background: #ffffff;
  border: 1px solid #dddddd;
  padding: 1rem;
  text-align: center;
}

.mmenu__mail {
  padding: 0rem 3.125rem;
  display: flex;
}

.mmenu__social {
  display: flex;
  justify-content: center;
  background: #ffffff;
  border: 1px solid #dddddd;
  padding: 1rem;
  margin-top: 3.5rem;
}

.mmenu__social p {
  color: #39464b;
}

.mmenu__close {
  left: auto;
  right: 1rem;
  top: 0.75rem;
  height: 40px;
  width: 40px;
  display: block;
  position: absolute;
}

.mmenu__close:before {
  position: absolute;
  display: block;
  width: 35px;
  font-family: 'MuseoLight';
  font-size: 40px;
  line-height: 40px;
  content: "\00d7";
  text-align: center;
  left: auto;
  right: 0;
  top: 0;
  height: 40px;
  color: #409bd5;
}

.block {
  display: flex;
}

