/* content */
.content__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 3.5rem 0;
}

.content__col {
  width: 100%;
}

.content__col--2col {
  width: 100%;
}

@media only screen and (min-width: 992px) {
  .content__col--2col {
    width: calc(50% - 2rem);
  }
}

.content__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 1rem 0;
}

.content__item {
  width: 100%;
  padding-left: 1.5rem;
  position: relative;
  margin: 1rem 0;
}

@media only screen and (min-width: 576px) {
  .content__item {
    width: calc(50% - 1rem);
  }
}

@media only screen and (min-width: 992px) {
  .content__item {
    width: calc(25% - 1rem);
  }
}

.content__item:after {
  position: absolute;
  top: 0.325rem;
  left: 0;
  content: ' ';
  width: 12px;
  height: 12px;
  background: #409bd5;
  border-radius: 50%;
  border: 3px solid #ffffff;
  box-shadow: 0 0 15px 0 #409bd5;
}

.content__title {
  font-size: 1.563rem;
  margin-bottom: 1.5rem;
}

.content__subtitle {
  font-size: 1.25rem;
  margin-bottom: 1.25rem;
}

.content__bold {
  font-family: 'MuseoMedium';
}

/* main-product */
.main-product {
  margin: 1.5rem 0 0 0;
}

@media only screen and (min-width: 768px) {
  .main-product {
    margin: 3rem 0;
  }
}

.main-product__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.main-product__nav {
  width: 100%;
}

@media only screen and (min-width: 768px) {
  .main-product__nav {
    width: 14.375rem;
  }
}

.main-product__carousel {
  width: calc(100% - 16rem);
  display: none !important;
}

@media only screen and (min-width: 768px) {
  .main-product__carousel {
    display: block !important;
  }
}

.main-product__cnav {
  width: 100%;
  margin-bottom: 2.813rem;
  display: none;
}

@media only screen and (min-width: 768px) {
  .main-product__cnav {
    display: block;
  }
}

.cnav {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cnav__arr {
  padding: 0.5rem 0.75rem;
  background: #dedede;
  display: flex;
  align-content: center;
  align-items: center;
  border-radius: 2px;
  opacity: 0.5;
  transition: opacity ease 0.2s;
}

.cnav__arr:hover {
  opacity: 1;
}

.cnav__arr--right {
  transform: rotate(180deg);
}

.cnav__icon {
  width: 1.75rem;
  height: 1.25rem;
  fill: #61696d;
}

.cnav__title {
  font-size: 1.125rem;
  font-family: 'MuseoBold';
  padding: 0 2.813rem;
}

@media only screen and (min-width: smpx) {
  .cnav__title {
    font-size: 1.563rem;
  }
}

.cnav__title--product {
  flex-basis: 100%;
  text-align: center;
}

.product-item {
  margin-bottom: 2rem;
  position: relative;
}

.product-item--catalog {
  width: 100%;
}

@media only screen and (min-width: 576px) {
  .product-item--catalog {
    width: calc(50% - 0.5rem);
  }
}

@media only screen and (min-width: 1200px) {
  .product-item--catalog {
    width: calc(33% - 0.5rem);
  }
}

.product-item__link {
  display: block;
  background: #ffffff;
}

.product-item__link:hover {
  color: #39464b;
}

.product-item__image img {
  display: block;
  width: 100%;
}

.product-item__desc {
  padding: 1rem;
  background: #ffffff;
}

.product-item__title {
  font-family: 'MuseoBold';
  line-height: 1.3;
}

.product-item__characteristics {
  margin: 0.5rem 0;
}

.product-item__characteristics-item {
  color: #a8a1ba;
  font-size: 0.875rem;
}

.product-item__characteristics-item span {
  color: #39464b;
}

.product-item__prices {
  display: flex;
  justify-content: space-between;
}

.product-item__price {
  color: #39464b;
}

.product-item__price--old span {
  text-decoration: line-through;
}

.product-item__price span {
  font-family: 'MuseoBold';
  font-size: 1.125rem;
}

.product-item__cost {
  font-size: 1.125rem;
  font-family: 'MuseoBold';
  color: #409bd5;
}

.product-item__currency {
  color: #409bd5;
}

.product-item__btns {
  display: flex;
  justify-content: space-between;
}

.product-item__btn {
  margin-top: 1rem;
  width: calc(50% - 0.375rem);
  font-family: 'MuseoBold';
  font-size: 0.75rem;
  text-transform: uppercase;
  padding: 0.75rem 0.75rem;
  text-align: center;
  display: inline-block;
}

.product-item__btn--favorite {
  background: #efefef;
}

.product-item__btn--cart {
  background: #409bd5;
  color: #ffffff;
}

.product-item__btn--cart:hover {
  background: #256086;
  color: #ffffff;
}

.product-item__remain {
  color: #39464b;
  text-align: center;
  margin-top: 1rem;
  font-size: 0.875rem;
}

.footer {
  background: #3a4247;
  padding: 2rem 0;
}

.footer .list a, .footer a {
  color: #e5edf0;
}

.footer .list a:hover, .footer a:hover {
  color: #ffffff;
}

.footer__wrap {
  display: flex;
  justify-content: space-between;
}

.footer__logo {
  margin-top: 0.25rem;
  margin-right: 2rem;
  max-width: 7rem;
}

.footer__nav {
  padding: 0;
  margin: 0;
  display: none;
}

@media only screen and (min-width: 768px) {
  .footer__nav {
    display: block;
  }
}

.footer__contact {
  display: none;
}

@media only screen and (min-width: 1200px) {
  .footer__contact {
    display: block;
    width: 13rem;
  }
}

.footer__city {
  display: flex;
}

.footer__city p {
  color: #e5edf0;
}

.footer__mail {
  display: flex;
}

.footer__phone {
  text-align: right;
}

.footer__tel {
  display: block;
  color: #e5edf0;
  margin-bottom: 0.125rem;
  font-family: 'MuseoMedium';
}
