.left-side {
    width: 100%;
    @include rmin($sm) {
        width: $leftSize; } }

.left-nav {
    margin-top: 2rem;
    display: none;
    @include rmin($sm) {
        display: block; }
    &__title {
        text-transform: uppercase;
        font-size: 1rem;
        font-family: 'MuseoBold';
        padding: 1rem 1.75rem;
        background: #ececec; }
    &__list {
        background: #ffffff;
        & li.active > a {
            color: $linkBlue; } }
    &__item {
        border-bottom: 1px solid #ececec;
        padding: 0.875rem 1.75rem;
        &:last-child {
            border: 0; }
        & > a {
            display: block;
            font-family: 'MuseoBold';
            & span {
                color: #999999; } }
        & ul {
            margin-top: 0.25rem;
            & li {
                &:before {
                    content: ' ';
                    display: inline-block;
                    margin-bottom: 0.125rem;
                    height: 0.375rem;
                    width: 0.375rem;
                    background-image: url('../img/circle.svg');
                    background-size: contain;
                    background-repeat: no-repeat;
                    padding-left: 0.75rem; }
                & a {
                    font-size: 0.875rem; } } } } }
