/* Constants */
$btn: #409bd5;
$btnHover: #256086;

$icon: #409bd5;

$link: #39464b;
$linkBlue: #409bd5;
$linkHover: #409bd5;

$text: #39464b;

$leftSize: 14.375rem;

/* media */
$xs: 576;
$sm: 768;
$md: 992;
$lg: 1200;


html, body {
    font-family: 'MuseoLight';
    font-size: 15px;
    line-height: 1.4;
    color: $text;
    background: #f9f9f9;
    @include rmin($xs) {
        font-size: 16px; } }

* {
    box-sizing: border-box; }

img {
    max-width: 100%; }

a {
    color: $link;
    text-decoration: none;
    @include tr(all ease 0.3s);
    &:hover {
        color: $linkHover; } }

p {
    color: #39464b;
    line-height: 1.375rem;
    margin-bottom: 1rem; }

.overflow {
    max-width: 100%;
    overflow-x: scroll; }

.icon {
    margin-right: 0.5rem;
    &__svg {
        width: 1rem;
        height: 1.25rem;
        fill: $icon;
        vertical-align: sub;
        &--auth {
            fill: #8daec2; } } }

.container {
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 10px;
    @include rmin($xs) {
        padding: 0 20px; } }

.title {
    font-size: 1.563rem;
    line-height: 1.3;
    margin-bottom: 1rem;
    font-family: 'MuseoBold'; }
.subtitle {
    font-size: 1.25rem;
    margin-bottom: 1.25rem; }
.date {
    font-size: 0.875rem;
    color: #929090; }
p {
    margin-bottom: 1rem; }
p+img {
    margin-bottom: 1rem; }


/* btn-big */
.btn-big {
    background: $btn;
    color: #ffffff;
    text-transform: uppercase;
    font-size: 0.875rem;
    font-family: 'MuseoMedium';
    padding: 0.825rem 3.25rem 0.825rem 1.25rem;
    display: inline-block;
    position: relative;
    @include rmin($sm) {
        padding: 1rem 3.75rem 1rem 1.75rem; }
    &:hover {
        background: $btnHover;
        color: #ffffff;
        &:after {
            transform: translateX(0.5rem); } }
    &:after {
        position: absolute;
        right: 1.75rem;
        top: calc(50% - 0.4125rem);
        width: 0.75rem;
        height: 1.25rem;
        content: ' ';
        background: url(../img/arr-right.svg) no-repeat;
        background-size: contain;
        opacity: 0.8;
        @include tr(transform ease 0.3s); }
    &--white {
        background: #ffffff;
        color: $btn;
        &:hover {
            background: $btnHover;
            color: #ffffff; }
        &:after {
            background: url(../img/arr-right-blue.svg) no-repeat;
            background-size: contain; } } }


/* subnav */
.subnav {
    display: none;
    @include rmin($md) {
        display: block; }
    &--footer &__item {
        margin-bottom: 0.25rem; }
    &--footer &__link {
        color: #e5edf0;
        text-decoration: none;
        border-bottom: 1px solid rgba(255,255,255,0.6);
        @include tr(all ease 0.2s);
        &:hover {
            border-bottom: 1px solid transparent; } } }

/* action-nav */
.action-nav {
    &__list {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row; }
    &__item {
        flex-grow: 1;
        flex: 1; }
    &__link {
        width: 100%;
        padding: .775rem;
        white-space: nowrap;
        &:after {
            display: none;
            @include rmin($sm) {
                display: block; } } } }


/* Breadcrumbs */
.breadcrumb {
    display: flex;
    flex-wrap: wrap;
    padding: 1.5rem 0;
    list-style: none;
    border-radius: .25rem;
    &__item {
        font-size: 0.875rem;
        &+&:before {
            display: inline-block;
            padding-right: .5rem;
            padding-left: .5rem;
            color: #6c757d;
            content: "/"; }
        & a {
            text-decoration: underline;
            font-size: 0.875rem; } } }

/* badges */
.badges {
    position: absolute;
    top: 0.5rem;
    left: 0.5rem;
    &__list {
        display: flex;
        flex-wrap: wrap; }
    &__item {
        margin-right: 1rem;
        background-size: contain;
        background-repeat: no-repeat;
        font-size: 0.875rem;
        color: #ffffff;
        padding: 0.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 3.5rem;
        height: 3.5rem;
        font-family: 'MuseoMedium';
        &--sale {
            background-image: url(../img/sticker.svg); } } }


/* fcart */
.fcart {
    &--product {
        background: transparent; }
    height: 100%;
    display: flex;
    background: #e6e6e6;
    align-items: center;
    &__btn {
        height: 100%;
        width: 100%;
        padding: 0.75rem 0.875rem 0.5rem 0.875rem;
        @include rmin($sm) {
            padding: 0.75rem 1.25rem; }
        display: flex;
        align-content: center;
        align-items: center;
        justify-content: center;
        position: relative;
        background: #e6e6e6;
        @include tr(all ease 0.3s);
        @include rmin($sm) {
            width: auto; }
        &:hover {
            background: $btn;
            color: #ffffff;
            fill: #ffffff; }
        &--product {
            margin-right: 1rem;
            background: $btn;
            color: #ffffff;
            fill: #ffffff;
            &:hover {
                background: $btnHover; } } }
    &__hr {
        width: 1px;
        height: 1.5rem;
        background: #cccccc; }
    &__icon {
        width: 1.125rem;
        height: 1.125rem; }
    &__name {
        font-size: 0.875rem;
        font-family: 'MuseoMedium';
        margin-bottom: 0.125rem;
        margin-left: 0.5rem;
        white-space: nowrap; } }

/* btn */
.btn {
    padding: 0.625rem 0.875rem 0.625rem 0.875rem;
    background: $btn;
    color: #ffffff;
    fill: #ffffff;
    white-space: nowrap;
    display: block;
    font-size: 1rem;
    font-family: 'MuseoLight';
    @include tr(all ease 0.25s);
    &--center {
        text-align: center; }
    &--bold {
        font-family: 'MuseoBold'; }
    &--inline {
        display: inline-block; }
    &:hover {
        background: $btnHover;
        color: #ffffff; } }

.btn-icon {
    height: 100%;
    @include rmin($sm);
    padding: 0.5rem 0.75rem 0.375rem 0.75rem;
    display: inline-block;
    position: relative;
    background: $btn;
    color: #ffffff;
    fill: #ffffff;
    white-space: nowrap;
    @include tr(all ease 0.3s);
    &:hover {
        background: $btnHover;
        color: #ffffff;
        fill: #ffffff; }
    & i {
        display: inline-block; }
    &__icon {
        width: 1.125rem;
        height: 1.125rem; }
    &__name {
        font-size: 0.875rem;
        font-family: 'MuseoMedium';
        margin-bottom: 0.125rem;
        margin-left: 0.5rem;
        white-space: nowrap;
        vertical-align: text-bottom;
        display: inline-block; } }

.btn-trash {
    &__icon {
        width: 1.5rem;
        height: 1.5rem;
        fill: #c02323; } }

/* Form */
.form {
    &__group {
        margin-bottom: 1.5rem;
        width: 100%;
        max-width: 30rem;
        &--center {
            text-align: center; } }
    &__label {
        font-size: 0.875rem;
        width: 100%;
        margin-bottom: 0.125rem; }
    &__input[type="text"] {
        font-size: 1rem;
        padding: 0.5rem 0.5rem;
        border: 1px solid #cecece;
        width: 100%; }
    &__btn {
        background: $btn;
        color: #ffffff;
        padding: 0.625rem 2rem;
        font-size: 0.875rem;
        &:hover {
            background: $btnHover; } }
    &__repass {
        margin-right: 2rem;
        color: $linkBlue; } }

/* list */
.list {
    & li {
        margin-bottom: 0.125rem; }
    & li a {
        color: $link;
        text-decoration: none;
        border-bottom: 1px solid rgba(255,255,255,0.6);
        @include tr(all ease 0.2s);
        &:hover {
            color: $linkHover;
            border-bottom: 1px solid transparent; } } }

.social-btn {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: flex-end;
    margin-top: 0.75rem;
    & p {
        color: #e5edf0;
        margin-bottom: 0;
        display: none;
        @include rmin($xs) {
            display: block; } }
    &__link {
        display: inline-flex;
        margin-left: 0.5rem;
        padding: 0.5rem;
        fill: #e5edf0;
        background: #4c5b65;
        @include tr(opacity ease 0.2s);
        &:hover {
            opacity: 0.5; } }
    &__svg {
        width: 14px;
        height: 14px; } }

/* Table */
.stable {
    & thead {
        border-bottom: 1px solid #cccccc;
        & td {
            padding: 0.75rem 0;
            white-space: nowrap; } }
    & tr {
        border-bottom: 1px solid #cccccc;
        &:last-child {
            border: 0; }
        & td {
            text-align: center;
            padding: 0.5rem 0.5rem;
            font-size: 0.875rem;
            vertical-align: middle;
            &:last-child {
                text-align: right; }
            &:first-child {
                text-align: left; }
            & img {
                max-width: 8rem; } } }
    &__img {
        width: 8.75rem; }
    &__title {
        text-decoration: underline;
        font-family: 'MuseoBold'; }
    &__remain {
        color: #555555;
        white-space: nowrap; }
    &__price {
        white-space: nowrap;
        font-family: 'MuseoBold'; }
    &__cprice {
        &-old {
            font-family: 'MuseoBold';
            color: #777777;
            text-decoration: line-through;
            white-space: nowrap; }
        &-sale {
            font-family: 'MuseoBold';
            white-space: nowrap; }
        &-price {
            font-family: 'MuseoBold';
            white-space: nowrap; } }
    &__sample {
        color: $linkBlue;
        &:hover {
            color: $link; } } }

/* count */
.count {
    display: flex;
    justify-content: center;
    @include rmin($sm) {
        justify-content: flex-start; }
    &--cart {
        justify-content: center; }
    &__btn {
        background: #ededed;
        padding: 0.75rem 1rem;
        font-size: 1rem;
        line-height: 1;
        display: inline-block;
        @include rmin($sm) {
            flex-grow: 0; } }
    &__count {
        background: #ffffff;
        padding: 0.75rem 1rem;
        font-size: 1rem;
        line-height: 1;
        text-align: center;
        max-width: 4rem; }

    &--cart &__btn {
        background: transparent;
        padding: 0 0.375rem;
        font-size: 1rem;
        font-family: 'MuseoBold'; }
    &--cart &__count {
        background: #ffffff;
        padding: 0.5rem 0.875rem;
        font-size: 0.875rem;
        width: 3rem;
        border: 1px solid #cccccc; } }


/* remodal */
.remodal {
    padding: 1.5rem 1.75rem 0.75rem;
    text-align: left;
    &--min {
        max-width: 500px; }
    &-close {
        left: auto;
        right: 1rem;
        top: 1rem;
        height: 40px;
        &:before {
            font-size: 40px;
            line-height: 40px;
            color: #000000; } }
    &__title {
        font-size: 1.25rem;
        font-family: 'MuseoBold';
        margin-bottom: 1.5rem;
        text-align: left; }
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between; }
    &__item {
        width: 100%;
        margin-bottom: 0.25rem;
        @include rmin($xs) {
            width: calc(100% / 2 - 2rem); }
        @include rmin($sm) {
            width: calc(100% / 3 - 2rem); }
        & a {
            font-family: 'MuseoMedium'; } }


    &__footer {
        margin-top: 1.5rem;
        padding-top: 0.75rem;
        border-top: 1px solid #cccccc;
        color: #999999;
        text-align: right;
        font-size: 0.875rem;
        &--center {
            text-align: center; } }
    &__svg {
        width: 2rem;
        height: 1.25rem;
        fill: #409bd5;
        vertical-align: bottom; } }


/* mobile-menu (mmenu) */
.mmenu {
    width: 87%;
    max-width: 25rem;
    overflow-y: scroll;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 15;
    background: #f6f6f6;
    transform: translateX(-26rem);
    @include tr(transform ease 0.2s);
    &__is-opened {
        box-shadow: 0 0 150px 10px rgba(0,0,0,.35);
        transform: translateX(0);
        @include rmin($sm) {
            box-shadow: none;
            transform: translateX(-26rem); } }
    &__top {
        display: flex;
        align-content: center;
        align-items: center;
        padding: 1.5rem 1rem 1rem 1rem;
        position: relative; }
    &__logo {
        max-width: 5rem;
        margin-top: 0.25rem; }
    &__phone {
        margin-left: 1.5rem !important; }

    &__auth {
        //margin-top: 0 !important
        padding: 0.75rem 0;
        text-align: left !important;
        background: #ffffff;
        border-top: 1px solid #dddddd;
        border-bottom: 1px solid #dddddd; }
    &__nav {
        padding: 1.25rem;
        margin: 1.5rem 1.75rem 0;
        background: #ffffff;
        border: 1px solid #dddddd;
        & .list {
            margin-left: 1.75rem; } }
    &__subnav {
        display: block; }
    &__city {
        padding: 0rem 3.125rem;
        margin-top: 1.5rem;
        display: flex; }
    &__city-select {
        background: #ffffff;
        border: 1px solid #dddddd;
        padding: 1rem;
        text-align: center; }
    &__mail {
        padding: 0rem 3.125rem;
        display: flex; }
    &__social {
        display: flex;
        justify-content: center;
        background: #ffffff;
        border: 1px solid #dddddd;
        padding: 1rem;
        margin-top: 3.5rem;
        & p {
            color: $text; } }


    &__close {
        left: auto;
        right: 1rem;
        top: 0.75rem;
        height: 40px;
        width: 40px;
        display: block;
        position: absolute;
        &:before {
            position: absolute;
            display: block;
            width: 35px;
            font-family: 'MuseoLight';
            font-size: 40px;
            line-height: 40px;
            content: "\00d7";
            text-align: center;
            left: auto;
            right: 0;
            top: 0;
            height: 40px;
            color: $linkBlue; } } }


